.Navbar {
	height: 8vh;
	color: white;
	border: .2rem solid black;
	font-size: 2rem;
	font-weight: 200;

	display: flex;
	justify-content: space-between;
	align-items: center;
}

.logo-box {
	background-color: green;
	padding: 1rem;
	/* height: 100%;
  width: 20%; */
	display: flex;
	justify-content: center;
	align-items: center;
	align-self: stretch;
	/* margin-left: 2rem; */
	position: relative;
	flex: 0 0 30%;

	/* box-shadow: 0 2rem 2rem rgba(0, 0, 0, .2); */
}

.logo-icon {
	background-image: linear-gradient(100deg, salmon 0%, salmon 50%, crimson 50%);
	height: 3rem;
	width: 3rem;
	border-radius: 20%;
	position: relative;
	transition: all .2s;
}

.logo-icon:hover {
	transform: rotate(20deg);
}

.logo-icon:hover + .logo-name {
	background-color: white;
	color: black;
	/* box-shadow: 1rem 1rem 2rem rgba(0, 0, 0, .2); */
}

