.logo-name {
	margin: 0 2rem;
	/* text-decoration: none; */
	transition: all 1s;
}

/* Navigation Box with all the Links */
.navigation-box {
	background-color: orangered;
	display: flex;
	justify-content: center;
	align-items: center;
	/* This part is so that the container itself stretches. */
	align-self: stretch;
}

.navigation-link {
	padding: 1rem;
	margin-right: 1rem;
	border: .3rem solid black;
	align-self: stretch;
	display: flex;
	align-items: center;
	/* box-shadow: 0 2rem 2rem rgba(0, 0, 0, .2); */
}
